<template>
  <div class="flex-page">
    <van-nav-bar
      @click-left="goBack"
      v-bind:class="{'bar-full-show':fullBar}"
      left-arrow
    > 
      <slot slot="left" name="left">
        <div class="nav-left">
          <img
              v-show="fullBar"
              class="nav-bar-custom-icon"
              src="../../assets/images/mall/navbar_icon_arrow_blackBg@2x.png"
              alt
            />
            <img
              v-show="!fullBar"
              class="nav-bar-custom-icon"
              src="../../assets/images/common/nav_return_black.png"
              alt
            />
        </div>
      </slot>
      <slot slot="right" name="right">
        <div class="quality-navbars">
          <div @click="collection">
            <img v-show="fullBar" class="nav-bar-custom-icon" :src="hasCollection ? fullCollectedIcon1 : fullCollectedIcon2" alt />
            <img v-show="!fullBar" class="nav-bar-custom-icon" :src="hasCollection ? normalCollectIcon1 : normalCollectIcon2" alt />
          </div>
          <div @click="wechartShare">
            <img
              v-show="fullBar"
              class="nav-bar-custom-icon"
              src="../../assets/images/mall/navbar_icon_share_blackBg.png"
              alt
            />
            <img
              v-show="!fullBar"
              class="nav-bar-custom-icon"
              src="../../assets/images/mall/mall_icon_share.png"
              alt
            />
          </div>
        </div>
      </slot>
      <slot slot="title" name="title">
        <div class="custom-title" :class="{customTitleThemeL:bindThemeL}" v-show="!fullBar">{{qualityDetail.title}}</div>
      </slot>
    </van-nav-bar>
    <div class="flex-full" ref="quality" id="flex-full">
      <section class="quality-main-image">
        <div v-if="articleImg">
          <img v-show="isShareOpen" style="width: 100%;" v-lazy="articleImg" :key="articleImg">
          <img v-show="!isShareOpen" style="width: 100%;" :src="articleImg">
        </div>
      </section>
      <section class="quality-detail">
        <div class="container-top">
          <p>{{qualityDetail.title}}</p>
          <div class="message" :class="{messageThemeL:bindThemeL}">
            <span>{{qualityDetail.publishTime}}</span>
            <span v-show="qualityDetail.readTimes != undefined">阅读{{qualityDetail.readTimes || 0}}</span>
          </div>
        </div>
        <div class="container-bottom">
          <!-- <div v-for="(item,index) in qualityDetail.goodsIdArr" :key="index"> -->
            <div v-html="articleContent"></div>
            <!-- <div class="goods-item" v-if="item.skuNo" @click="payGoods(item)">
              <div class="left">
                <img v-lazy="item.spuPicUrl" :key="item.spuPicUrl"/>
              </div>
              <div class="center">
                <p class="title">{{item.spuName}}</p>
                <p class="desc">{{item.spuSalePoint}}</p>
                <p class="price">￥{{item.skuPrice}}</p>
              </div>
              <div class="right">
                <div class="goods-btn">立即购买</div>
                <div class="car-btn" @click.stop="addToChart(item)">加入购物车</div>
              </div>
            </div> -->
          <!-- </div> -->
        </div>
      </section>
    </div>
    <van-action-sheet v-model="show.weChartShare" cancel-text="取消" @cancel="onCancel" round safe-area-inset-bottom>
      <p class="shareTit">分享至</p>
      <div class="share-box" :style="{'display':'flex','justify-content':'center'}">
        <div class="shareChart" @click="checkStoragePermissions(1)">
          <img :src="icon.shareChart" />
          <p>微信好友</p>
        </div>
        <div class="shareFriends">
          <img :src="icon.shareFriends" @click="checkStoragePermissions(2)"/>
          <p>朋友圈</p>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { ImagePreview, Toast, Dialog } from "vant";
import Vue from "vue";
import infiniteScroll from "../../components/infiniteScroll/infiniteScroll";
import { addToCart } from "../../services/productInfoService";
export default {
  name: "QualityDetailPage",
  created() {},
  mounted() {},

  props: {
    activeIndex: {
      type: Number
    }
  },
  data() {
    return {
      toHomePage: false,
      fullBar: true,
      fullCollectIcon: "",
      normalCollectIcon: "",
      qualityTitle: "品质生活标题",
      qualityDetail: {},
      articleNo: '',
      hasCollection: "",
      contentShow: false,
      scrollTop: 0,
      //微信分享链接打开
      isShareOpen: false,
      //文章头图
      articleImg: '',
      //文章内容
      articleContent: '',
      tmpContent: '',
      //商品列表
      goodsItems: [],
      //为0时内容渲染
      contentDrawing: 100,
      show: {
        weChartShare: false //微信分享
      },
      icon: {
        shareChart: require("@/assets/images/common/nav_icon_share_wechat.png"),
        shareFriends: require("@/assets/images/common/nav_icon_share_moments.png")
      },
      isFirst: true,
      clickNum: 1,
      fullCollectedIcon1: require("../../assets/images/mall/nav_icon_collect_sel@2x.png"),
      fullCollectedIcon2: require("../../assets/images/mall/navbar_icon_collection_blackBg@2x.png"),
      normalCollectIcon1: require("../../assets/images/mall/navbar_icon_collection_sel.png"),
      normalCollectIcon2: require("../../assets/images/mall/navbar_icon_collection_black@2x.png"),
      hasStoragePermission:false,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector("#flex-full").scrollTop;
    //进入商品详情页需要保留页面缓存，返回上级页面需要清空缓存（首页、我的收藏）
    this.toHomePage = to.path == "/homePage" || to.path == "/homePageThemeL" || to.path == "/myCollect"
   
    next();
  },
  computed: {
    ...mapState({
      setQualityCode: state => state.productPage.setQualityCode,
    }),
    setQualityDetalPopupFlag() {
      return this.$store.state.quality.qualityDetalPopupFlag;
    }
  },
  watch: {
    setQualityCode:{
        handler(newval, oldval) {
            if(newval != this.articleNo&&newval!=-1){
                this.articleNo=newval;
                this.getArticleDetailNew();
                this.$store.commit("setQualityCodeChange", {
                    
                  qualityCode: -1
                });
            }
        },
    },
    /*记录当前页面有弹窗*/
    setQualityDetalPopupFlag(val) {
      console.log("监听被调用");
      if (val) {
        this.$store.dispatch("setQualityDetalPopupFlag", false);
        if (this.show.weChartShare) {
          this.show.weChartShare = false;
        } else {
          this.goBack();
        }
      }
    },
    contentDrawing(val) {
      // console.log('contentDrawing=========>>>>> val： ' + val);
      if (val == 0) {
        this.articleContent = this.tmpContent;
        this.addGoodsClickedEven();
      }
    }
  },
  activated() {
    if (this.$route.params.articleNo) {
      this.articleImg = '';
      this.articleContent = '';
      this.qualityDetail = {};
      this.articleNo = this.$route.params.articleNo;
      let articleImg = this.$route.params.articleImg;
      // console.log('===>>> 当前传过来的商品id：' + this.articleNo );
      this.isShareOpen = false;
      if (articleImg && articleImg != undefined) {
        if (articleImg == 'share') {
          this.isShareOpen = true;
          this.articleImg = '';
        }
        else {
          this.articleImg = articleImg;
        }
      }
      document.querySelector("#flex-full").scrollTop = this.scrollTop = 0;
      this.getArticleDetail();
      this.listenerScrollAction();
    } else {
      document.querySelector("#flex-full").scrollTop = this.scrollTop;
    }
  },
  deactivated () {
    if (this.toHomePage) {
      this.articleImg = '';
      this.articleContent = '';
      this.qualityDetail = {};
      this.fullBar = true;
      this.contentDrawing = 100;
      this.hasCollection = false;
    }
  },
  methods: {
    ...mapActions([
      "getQualityDetailAction",
      "getQualityGoodsAction",
      "saveCollectAction",
      "getCollectAction",
      "deleteCollectAction",
      "getShareArticleUrlAction",
      "getReadInfoAction"
    ]),
    //添加商品标签的点击事件
    addGoodsClickedEven() {
      let that = this;
      this.$nextTick(()=>{
        var goodsList = document.querySelectorAll('.goods-item');
        for(let i = 0; i<goodsList.length; i++){
          let goodItem = goodsList[i];
          let index = goodItem.getAttribute('goods-index');
          let item = that.goodsItems[index];
          //商品标签点击事件
          goodItem.onclick=function(){
            // console.log('点击商品标签')
            if (item != 'tmp') {
              that.payGoods(item);
            }
          }

          //添加购物车点击事件
          let carBtn = goodItem.children[2].children[1];
          carBtn.onclick=function(e){
            e.stopPropagation() //阻止事件冒泡
            // console.log('点击加入购物车')
            if (item != 'tmp') {
              that.addToChart(item);
            }
          }
        }
      })
    },
    //更新详情图片缩略图为原图
    updateArticleImg(imgSrc, articleNo) {
      var img = new Image(); 
      let that = this;
      img.onload = function(){
        if (articleNo == that.articleNo) {
          that.articleImg = imgSrc;
          // console.log('===>>>>> onload 更换高清图片:' + that.articleNo);
        }
      }
      img.src = imgSrc;
    },
    /**
     * 获取当前文章详情内容
     *
     **/
    getArticleDetail() {
      this.getQualityDetailAction(this.$route.params.articleNo)
        .then(data => {
          if(data.articleNo == this.articleNo) {
            this.getReadInfo();
            this.getGoodsInfo(data.goodsIdArr, data.content);
            this.qualityDetail = data;
            
            // 微信分享拉起时，没有传图片
            if (this.isShareOpen) {
              this.articleImg = this.qualityDetail.picUrl;
            }
            else {
              this.updateArticleImg(this.qualityDetail.picUrl, data.articleNo);
            }
            return this.getCollectAction({
              collectType: "03",
              userCode: this.$userInfo.OWNER_CODE,
              contentCode: this.qualityDetail.articleNo
            });
          }
        })
        .then(list => {
          if (list.length > 0) {
            this.collectCode = list[0].collectCode;
            this.hasCollection = list.some(val => val.collectCode);
          } else {
            this.hasCollection = false;
          }
        })
        .catch(error => {
          if(error.indexOf("网络异常") != -1) {
            this.$toast("网络异常，请稍后重试！");
            this.goBack();
            return;
          }
          Dialog.alert({
            message: error
          }).then(() => {
            this.goBack();
          });
        });
    },
    getArticleDetailNew() {
      this.contentDrawing = 100;
      this.getQualityDetailAction(this.articleNo)
        .then(data => {
          if(data.articleNo == this.articleNo) {
            this.getReadInfo();
            this.getGoodsInfo(data.goodsIdArr, data.content);
            this.qualityDetail = data;
            this.$forceUpdate();
            // 微信分享拉起时，没有传图片
            if (this.isShareOpen) {
              this.articleImg = this.qualityDetail.picUrl;
            }
            else {
              this.updateArticleImg(this.qualityDetail.picUrl, data.articleNo);
            }
            return this.getCollectAction({
              collectType: "03",
              userCode: this.$userInfo.OWNER_CODE,
              contentCode: this.qualityDetail.articleNo
            });
          }
        })
        .then(list => {
          if (list.length > 0) {
            this.collectCode = list[0].collectCode;
            this.hasCollection = list.some(val => val.collectCode);
          } else {
            this.hasCollection = false;
          }
        })
        .catch(error => {
          if(error.indexOf("网络异常") != -1) {
            this.$toast("网络异常，请稍后重试！");
            this.goBack();
            return;
          }
          Dialog.alert({
            message: error
          }).then(() => {
            this.goBack();
          });
        });
    },

    /**
     * 获取阅读信息，增加阅读数量
     *
     **/
    getReadInfo() {
      this.getReadInfoAction(this.$route.params.articleNo).then(
        data => {},
        error => {}
      );
    },

    /**
     * 获取当前文章详情内容
     *
     **/
    listenerScrollAction() {
      this.$nextTick(() => {
        let quaDom = this.$refs.quality;
        let self = this;
        quaDom.onscroll = e => {
          if (quaDom.scrollTop < 100) {
            self.fullBar = true;
          } else {
            self.fullBar = false;
          }
        };
      });
    },

    /**
     * 添加购物车
     */
    addToChart(item) {
      //添加购物车
      if (item.storeNumber != 0) {
        const chartData = {
          ownerCode: this.$userInfo.OWNER_CODE,
          spuCode: item.spuCode,
          skuNo: item.skuNo,
          shopCode: item.shopCode,
          skuCount: 1,
          mark: 1
        };
        addToCart(chartData).then(response => {
          if (response.resultCode == "0") {
            this.$toast("添加购物车成功");
          } else if (
            response.resultCode == "20100" &&
            response.data.result == "0"
          ) {
            this.$toast("该商品添加购物车的数量已达上限！");
          }
        });
      } else {
        Dialog({ message: "该商品补货中\n再看看其他的吧" });
      }
    },

    /**
     * 获取富文本中包含的商品信息
     * @param
     * goodsData   Array<Object>  当前富文本组合对象
     *
     **/
    getGoodsInfo(goodIds, content) {
      this.tmpContent = content;
      //未添加商品信息直接渲染内容
      if (goodIds.length == 0) {
        //渲染html
        this.contentDrawing = 0;
        return;
      }

      //重新拼装商品信息html
      this.contentDrawing = goodIds.length;
      this.goodsItems = [];
      let self = this;
      let articleNo = this.articleNo;
      for (let index = 0; index < goodIds.length; index++) {
        const goodId = goodIds[index];
        this.goodsItems.push('tmp');
        if (goodId) {
          let goodIdStr = 'GOOD_ID=' + goodId;
          this.getQualityGoodsAction({
            areaCode: this.$userInfo.AREA_CODE,
            spuCodeList: goodId
          })
          .then(res => {
            //丢弃网络慢重复进出页面缓存请求
            if (articleNo != self.articleNo) {
              return;
            }

            if (res && res.length > 0) {
              // let newGoodsData = Object.assign(goodsData[index], res[0]);
              // this.$set(this.qualityDetail.goodsIdArr, index, newGoodsData);
              let item = res[0];
              self.goodsItems[index] = item;
              let goodsHtml = `
                <div class="goods-item" goods-index="${index}" ref="goodsItem" style="height: 90px;background: #ffffff;box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);border-radius: 3px;padding: 12px;margin: 12px 0;display: flex;"">
                  <div class="left">
                    <img src="${item.spuPicUrl}" style="height: 90px;width: 90px;border-radius: 3px;"/>
                  </div>
                  <div class="center" style="width: 125px;padding: 0 8px 0 12px;overflow: hidden;height: 90px;">
                    <p class="title" style="margin: 0;font-size: 16px;color: #333333;letter-spacing: -0.06px;overflow: hidden; text-overflow: ellipsis;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 2;line-height: 22px;font-weight: bold;">${item.spuName}</p>
                    <p class="desc" style="font-size: 13px;color: #9a9a9a;letter-spacing: -0.05px;width: 100%;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;margin: 5px 0;line-height: 18px;">${item.spuSalePoint}</p>
                    <p class="price" style="margin: 0;font-weight: bold;margin-top: 6px;font-size: 14px;color: #f43d3d;line-height: 20px;letter-spacing: -0.04px;">￥${item.skuPrice}</p>
                  </div>
                  <div class="right" style="display: flex;flex-direction: column;justify-content: center;align-items: flex-end;">
                    <div class="goods-btn" style="background-image: linear-gradient(-143deg, #ff9ba5 0%, #fb5364 82%);border-radius: 20px;font-size: 13px;justify-content: center;line-height: 30px;color: #ffffff;text-align: center;width: 76px;height: 30px;">立即购买</div>
                    <div class="car-btn" style="border: 1px solid #fb5768;border-radius: 20px;box-sizing: border-box;line-height: 30px;color: #ffffff;text-align: center;width: 76px;height: 30px;font-size: 13px;color: #ff5050;margin-top: 10px;">加入购物车</div>
                  </div>
                </div>`;
              if (this.$curTheme == "1") {
                //价格class="price"的字号由14变为15
                goodsHtml = `
                <div class="goods-item" goods-index="${index}" ref="goodsItem" style="height: 90px;background: #ffffff;box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);border-radius: 3px;padding: 12px;margin: 12px 0;display: flex;"">
                  <div class="left">
                    <img src="${item.spuPicUrl}" style="height: 90px;width: 90px;border-radius: 3px;"/>
                  </div>
                  <div class="center" style="width: 125px;padding: 0 8px 0 12px;overflow: hidden;height: 90px;">
                    <p class="title" style="margin: 0;font-size: 16px;color: #333333;letter-spacing: -0.06px;overflow: hidden; text-overflow: ellipsis;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 2;line-height: 22px;font-weight: bold;">${item.spuName}</p>
                    <p class="desc" style="font-size: 13px;color: #9a9a9a;letter-spacing: -0.05px;width: 100%;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;margin: 5px 0;line-height: 18px;">${item.spuSalePoint}</p>
                    <p class="price" style="margin: 0;font-weight: bold;margin-top: 6px;font-size: 15px;color: #f43d3d;line-height: 20px;letter-spacing: -0.04px;">￥${item.skuPrice}</p>
                  </div>
                  <div class="right" style="display: flex;flex-direction: column;justify-content: center;align-items: flex-end;">
                    <div class="goods-btn" style="background-image: linear-gradient(-143deg, #ff9ba5 0%, #fb5364 82%);border-radius: 20px;font-size: 13px;justify-content: center;line-height: 30px;color: #ffffff;text-align: center;width: 76px;height: 30px;">立即购买</div>
                    <div class="car-btn" style="border: 1px solid #fb5768;border-radius: 20px;box-sizing: border-box;line-height: 30px;color: #ffffff;text-align: center;width: 76px;height: 30px;font-size: 13px;color: #ff5050;margin-top: 10px;">加入购物车</div>
                  </div>
                </div>`;
              }

              self.tmpContent = self.tmpContent.replace(new RegExp(goodIdStr,'g'), goodsHtml);
              self.contentDrawing--;
              // console.log('有商品goodId： ' + goodIdStr);
            }
            else {
              self.tmpContent = self.tmpContent.replace(goodIdStr, '');
              self.contentDrawing--;
              // console.log('无商品goodId： ' + goodIdStr);
            }
          })
          .catch(error => {
            this.$toast(error || "网络异常");
          });
        }
      }
    },

    /**
     * 跳转至商品页面进行购买
     * @param
     * item     商品对象
     *
     **/
    payGoods(item) {
      console.log('===>>>> payGoods')
      if (item.storeNumber != 0) {
        this.$router.push({
          name: "goodsDetail",
          params: {
            spuCode: item.spuCode
          }
        });
      } else {
        Dialog({ message: "该商品补货中\n再看看其他的吧" });
      }
    },

    wechartShare() {
      // 点击分享弹窗
      this.show.weChartShare = true;
    },
    //取消微信分享
    onCancel() {
      this.show.weChartShare = false;
    },
    shareAction(x){
        if (this.hasStoragePermission) {
          this.shareToFriend(x)
        }else{
          Dialog.confirm({
          message: "请您打开存储权限，以正常使用分享功能"
        })
          .then(() => {
            this.requestStoragePermissions(x);
          })
          .catch(() => {
            //oncancel
          });
        }
      },
    //分享到朋友圈或者微信好友
    shareToFriend(x) {
      // andorid有问题
      // var list = ["android.permission.ANDROID_WRITE_EXTERNAL_STORAGE", "android.permission.CAMERA"];
      // var permissions = cordova.plugins.permissions;
      // permissions.requestPermissions(
      //   list,
      //   status => {
      //     if (status.hasPermission) {
      const data = {
        ownerCode: this.$userInfo.OWNER_CODE,
        areaCode: this.$userInfo.AREA_CODE,
        shareTo: x,
        articleNo: this.qualityDetail.articleNo
      };
      this.getShareArticleUrlAction(data).then(response => {
        if (response.resultCode == "0") {
          this.show.weChartShare = false;
          this.shareUrl = response.data;
          Wechat.share(
            {
              message: {
                title: this.qualityDetail.title,
                description: "更多精彩，尽在信我家",
                thumb: this.qualityDetail.picUrl,
                media: {
                  type: Wechat.Type.WEBPAGE,
                  webpageUrl: this.shareUrl
                }
              },
              scene: x == 1 ? Wechat.Scene.SESSION : Wechat.Scene.TIMELINE //1是微信好友 2是朋友圈
            },
            () => {
              //alert("Success");
            },
            reason => {
              this.$toast(reason); //无微信时提示用户未安装微信
            }
          );
        } else {
          this.show.weChartShare = false;
          this.$toast("分享失败，请稍后重试");
        }
      });
      //     }
      //   },
      //   status => {
      //     this.$toast("请打开图片权限");
      //   }
      // );
    },
    checkStoragePermissions(x) {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareAction(x);
      } else {
        var list = "android.permission.WRITE_EXTERNAL_STORAGE";
        var permissions = cordova.plugins.permissions;
        permissions.checkPermission(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareAction(x);
            } else {
              _this.hasStoragePermission = false;
              _this.shareAction(x);
            }
          },
          function() {
            _this.hasStoragePermission = false;
            _this.shareAction(x);
          }
        );
      }
    },
    requestStoragePermissions(x) {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareAction(x);
      } else {
        var list = ["android.permission.WRITE_EXTERNAL_STORAGE"];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareAction(x);
            } else {
              _this.hasStoragePermission = false;
            }
          },
          function() {
            _this.hasStoragePermission = false;
          }
        );
      }
    },
    goBack() {
      // this.qualityDetail = {};
      this.$router.back(-1);
    },
    collection() {
      if (!this.hasCollection) {
        this.hasCollection = true;
        this.saveCollection();
      } else {
        this.hasCollection = false;
        this.deleteCollect();
      }
    },
     
    /**
     * 保存收藏
     */
    saveCollection() {
      // 关闭loading
      this.isFirst = false;
      this.saveCollectAction({
        collectType: "03",
        userCode: this.$userInfo.OWNER_CODE,
        contentCode: this.qualityDetail.articleNo
      })
        .then(result => {
          this.isFirst = true;
          this.clickNum = 1;
          this.collectCode = result.collectCode;
        })
        .catch(error => {
          if (!this.isFirst && this.clickNum == 1) {
            // 如果是第一次再执行一遍
            this.clickNum = this.clickNum + 1;
            this.saveCollection();
          } else {
            this.isFirst = true;
            this.clickNum = 1;
            this.hasCollection = false;
            this.$toast(error);
          }
        });
    },

    /**
     * 取消收藏
     */
    deleteCollect() {
      this.isFirst = false;
      this.deleteCollectAction(this.collectCode)
        .then(result => {
          this.isFirst = true;
          this.clickNum = 1;
        })
        .catch(error => {
          if (!this.isFirst && this.clickNum == 1) {
            // 如果是第一次再执行一遍
            this.clickNum = this.clickNum + 1;
            this.deleteCollect();
          } else {
            this.isFirst = true;
            this.clickNum = 1;
            this.hasCollection = true;
            this.$toast(error);
          }
        });
    }
  }
};
</script>

